.submenu {
	background: $bgc;
	// border-radius: 15px
	box-shadow: $bs;
	position: relative;
	transform: translate3d(.5px, 0, 0);
	text-align: center;
	// padding: .5rem 0

	&:before {
		// content: ""
		top: 0;
		position: absolute;
		left: 0;
		height: 3px;
		width: 100%;
 }		// background: linear-gradient(to right, rgb(96, 58, 117), rgb(77, 63, 128));

	&__cont {
		position: absolute;
		top: 100%;
		left: 50%;
		transform: translate3d(-50%, 0, 0);
		padding: 2rem 0 0;
		opacity: 0;
		transition: opacity .3s ease;
		pointer-events: none;
		width: 200px;
		transition-delay: 0s; }

	&__el {
		position: relative;

		&:not(:last-child) {
			border-bottom: 1px solid #000; }

		&:before {
			content: "";
			background: linear-gradient(to right, $main-color, $second-color);
			top: 0;
			left: 0;
			position: absolute;
			@include size(100%);
			transform-origin: left;
			transition: transform .3s ease;
			transform: scale3d(0, 1, 1); }

		&:hover, &:focus {

			&:before {
				transform: scale3d(1, 1, 1); } } }

	&__link {
		color: #fff;
		text-decoration: none;
		display: block;
		padding: 10px 15px;
		position: relative;
		z-index: 1;

		&.active {
			// font-weight: bold
			background: linear-gradient(to right, $main-color, $second-color); } } }

.menu {
	display: flex;

	&__link {
		color: #fff;
		text-decoration: none;
		position: relative;

		&:after {
			content: "";
			position: absolute;
			left: 8px;
